module.exports = "## 背景\n之前讲了讲Parcel搭建React环境...想了想不讲一哈Webpack还真有点过意不去\n\n## 开始\n\n### 安装\n\n```\nnpm install webpack webpack-cli -g \n\n# 或者\nyarn global add webpack webpack-cli\n```\n\n### 配置\n\n创建`package.json`文件\n```\nnpm install webpack -D \n\n# 或者\nyarn add webpack -D\n```\n\n添加`npm scripts`\n```\n\"scripts\": {\n  \"build\": \"webpack --mode production\"\n},\n\"devDependencies\": {\n  \"webpack\": \"^4.1.1\",\n  \"webpack-cli\": \"^2.0.12\",\n}\n```\n\n#### 入口\n\n随意创建`./src/index.js`，此为webpack默认入口文件，若想更改可以对webpack配置文件进行修改\n\n```JSON\nmodule.exports = {\n  entry: './src/index.js' \n}\n\n// 上述配置等同于\nmodule.exports = {\n  entry: {\n    main: './src/index.js'\n  }\n}\n\n// 或者配置多个入口\nmodule.exports = {\n  entry: {\n    foo: './src/page-foo.js',\n    bar: './src/page-bar.js', \n    // ...\n  }\n}\n\n// 使用数组来对多个文件进行打包\nmodule.exports = {\n  entry: {\n    main: [\n      './src/foo.js',\n      './src/bar.js'\n    ]\n  }\n}\n```\n#### loader\n> 我们可以把 loader 理解为是一个转换器，负责把某种文件格式的内容转换成 webpack 可以支持打包的模块\n\n在module.rules中进行修改\n```Json\nmodule: {\n  // ...\n  rules: [\n    {\n      test: /\\.jsx?/, // 匹配文件路径的正则表达式，通常我们都是匹配文件类型后缀\n      include: [\n        path.resolve(__dirname, 'src') // 指定哪些路径下的文件需要经过 loader 处理\n      ],\n      use: 'babel-loader', // 指定使用的 loader\n    },\n  ],\n}\n```\n\n关于loader的安装可以查看[网址](https://doc.webpack-china.org/loaders/babel-loader/)\n\n#### plugin\n> 模块代码转换的工作由 loader 来处理，除此之外的其他任何工作都可以交由 plugin 来完成，只需在配置中通过 plugins 字段添加新的 plugin 即可\n\n```Json\nconst UglifyPlugin = require('uglifyjs-webpack-plugin')\n\nmodule.exports = {\n  plugins: [\n    new UglifyPlugin()\n  ],\n}\n```\n\n#### 输出\n> webpack 的输出即指 webpack 最终构建出来的静态文件，使用output字段进行修改\n\n```Json\nmodule.exports = {\n  // ...\n  output: {\n    path: path.resolve(__dirname, 'dist'),\n    filename: 'bundle.js',\n  },\n}\n\n// 或者多个入口生成不同文件\nmodule.exports = {\n  entry: {\n    foo: './src/foo.js',\n    bar: './src/bar.js',\n  },\n  output: {\n    filename: '[name].js',\n    path: __dirname + '/dist',\n  },\n}\n\n// 路径中使用 hash，每次构建时会有一个不同 hash 值，避免发布新版本时线上使用浏览器缓存\nmodule.exports = {\n  // ...\n  output: {\n    filename: '[name].js',\n    path: __dirname + '/dist/[hash]',\n  },\n}\n```\n\n#### 小结\n创建一个简单的配置文件webpack.config.js\n\n```Json\nconst path = require('path')\nconst UglifyPlugin = require('uglifyjs-webpack-plugin')\n\nmodule.exports = {\n  entry: './src/index.js',\n\n  output: {\n    path: path.resolve(__dirname, 'dist'),\n    filename: 'bundle.js',\n  },\n\n  module: {\n    rules: [\n      {\n        test: /\\.jsx?/,\n        include: [\n          path.resolve(__dirname, 'src')\n        ],\n        use: 'babel-loader',\n      },\n    ],\n  },\n\n  // 代码模块路径解析的配置\n  resolve: {\n    modules: [\n      \"node_modules\",\n      path.resolve(__dirname, 'src')\n    ],\n\n    extensions: [\".wasm\", \".mjs\", \".js\", \".json\", \".jsx\"],\n  },\n\n  plugins: [\n    new UglifyPlugin(), \n    // 使用 uglifyjs-webpack-plugin 来压缩 JS 代码\n    // 如果你留意了我们一开始直接使用 webpack 构建的结果，你会发现默认已经使用了 JS 代码压缩的插件\n    // 这其实也是我们命令中的 --mode production 的效果，后续的小节会介绍 webpack 的 mode 参数\n  ],\n}\n```\n\n> 简单的webpack搭建就到这里"

