module.exports = "## 背景\n我从不关注代码之外的东西，因为我相信有人会帮我做这些... \n\n之前上手了Parcel感觉还OK，在这里小小的整理一哈\n\n## 开始操作\n\n+ 安装Parcel\n\n```JavaScript\nyarn global add parcel-bundler\n```\n+ 创建package.json再顺便加个命令\n\n```JavaScript\nyarn init -y\n// package.json\n\"scripts\": {\n  \"start\": \"parcel index.html\"\n}\n```\n+ 安装Babel\n\n```JavaScript\nyarn add babel-preset-env\n```\n+ 安装node-sass\n\n```JavaScript\nyarn add node-sass\n```\n+ 接着创建.babelrc\n\n```Json\n{\n  \"preset\": [\"env\"]\n}\n```\n+ 安装React依赖\n\n```JavaScript\nyarn add react\nyarn add react-dom\nyarn add --dev parcel-bundler\nyarn add --dev babel-preset-env\nyarn add --dev babel-preset-react\n```\n+ 在.babelrc中修改\n\n```Json\n{\n  \"presets\": [\"env\", \"react\"]\n}\n```\n\n+ 让`React`内部`Class`支持箭头函数\n\n```\nnpm install --save-dev babel-plugin-transform-class-properties\n```\n\n+ 在.babelrc中添加\n\n```Json\n\"presets\": [\"env\", \"react\"],\n\"plugins\": [\n    [\"transform-class-properties\", { \"spec\": true }]\n  ]\n```\n再整理一下目录...现在应该是这样的(关键的)\n\n```Json\nnode_modules\nsrc\n    components\n    containers\n    App.js\n    App.scss\n.babelrc\nindex.html\npackage.json\n```\n\n**index.html**\n```Html\n<!DOCTYPE html>\n<html lang=\"en\">\n<head>\n  <meta charset=\"UTF-8\">\n  <meta name=\"viewport\" content=\"width=device-width, initial-scale=1.0\">\n  <meta http-equiv=\"X-UA-Compatible\" content=\"ie=edge\">\n  <title>Parcel-React</title>\n</head>\n<body>\n  <div id=\"root\"></div>\n  <script src=\"./src/index.js\"></script>\n</body>\n</html>\n```\n\n**index.js**\n```JavaScript\nimport React from 'react';\nimport ReactDOM from 'react-dom';\nimport App from './App';\n\nReactDOM.render(<App />, document.getElementById('root'));\n```\n**App.js**\n```JavaScript\nimport React, { Component } from 'react';\n\nimport './App.scss';\n\nclass App extends Component {\n  render() {\n    return (\n      <div className=\"App\">\n        <h1>Hello Parcel-React</h1>\n      </div>\n    );\n  }\n}\n\nexport default App;\n```\n**App.scss**\n```Css\n$color-red: red;\n$color-black: black;\nh1{\n  color: $color-red;\n  cursor: pointer;\n  &:hover{\n    color: $color-black;\n  }\n}\n```\n**package.json**\n```Json\n{\n  \"name\": \"parcel-test1\",\n  \"version\": \"1.0.0\",\n  \"main\": \"index.js\",\n  \"license\": \"MIT\",\n  \"scripts\": {\n    \"start\": \"parcel index.html\"\n  },\n  \"dependencies\": {\n    \"babel-preset-env\": \"^1.6.1\",\n    \"node-sass\": \"^4.8.3\",\n    \"react\": \"^16.2.0\",\n    \"react-dom\": \"^16.2.0\"\n  },\n  \"devDependencies\": {\n    \"babel-core\": \"^6.26.0\",\n    \"babel-preset-react\": \"^6.24.1\",\n    \"parcel-bundler\": \"^1.6.2\"\n  }\n}\n```\n\n最后`npm run`一哈\n\n![Parcel-React-Gif](http://p5sf6v0wz.bkt.clouddn.com/Jietu20180325-224425.gif\n)\n\n舒服了，终于可以不用手脚架了，自己动手丰衣足食\n\n> 有什么其他依赖再自己yarn add或npm install就好啦\n\n\n\n\n"

